import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalEventsManager } from '../core/global-events-manager.service';
@Component({
  selector: 'atl-reader',
  templateUrl: './reader.component.html',
  styleUrls: ['./reader.component.css']
})
export class ReaderComponent implements OnInit {
  constructor(private globalEventsManager: GlobalEventsManager, private router: Router) {
    this.globalEventsManager.viewBookDetailsClickedEmitter.subscribe((isbnValue) => {
      if (isbnValue) {
        this.router.navigateByUrl(`/reader/books/${isbnValue}`);
      }
    });
  }

  ngOnInit() {
  }

}



