import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { GlobalEventsManager } from '../core/global-events-manager.service';
@Component({
  selector: 'atl-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit {
  allBooksBoxVisible = true;
  allUsersBoxVisible = false;
  constructor(private globalEventsManager: GlobalEventsManager, private router: Router) {
    this.globalEventsManager.addBookClickedEmitter.subscribe((mode) => {
      if (mode) {
        this.onAddBookClicked();
      }
    });
  }

  ngOnInit() {
  }

  showAllBooksBox(): void {
    this.allBooksBoxVisible = true;
    this.allUsersBoxVisible = false;
  }
  showAllUsersBox(): void {
    this.allBooksBoxVisible = false;
    this.allUsersBoxVisible = true;
  }

  onAddBookClicked() {
    this.router.navigateByUrl('/admin/books/add-book');
  }
}



