import { Component, OnInit, Input } from '@angular/core';
import { GlobalEventsManager } from '../../../core/global-events-manager.service';
import { Book } from '../../../core/book';

@Component({
  selector: 'atl-book-thumbnail',
  templateUrl: './book-thumbnail.component.html',
  styleUrls: ['./book-thumbnail.component.css']
})
export class BookThumbnailComponent {
  @Input() book: Book;
  constructor(private globalEventsManager: GlobalEventsManager) { }

  onViewBookClicked(isbn): void {
    this.globalEventsManager.viewBookDetailsClicked(isbn);
  }
}



