import { Component, OnInit, Input, Pipe, PipeTransform } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { AuthenticationService } from '../../core/authentication.service';
import { BooksService } from '../../core/books.service';
import { Book } from '../../core/book';

@Pipe({
  name: 'searchFilter'
})
export class SearchFilter implements PipeTransform {
  transform(items: any[], criteria: any): any {
    return items.filter(item => {
      return (item.title.toLowerCase().includes(criteria.toLowerCase())) || (this.containsAuthor(item.authors, criteria.toLowerCase()));
    });
  }
  containsAuthor(authors, authorCriteria) {
    let found = false;
    authors.forEach(author => {
      found = author.toLowerCase().includes(authorCriteria);
    });
    return found;
  }
}

@Component({
  selector: 'atl-all-books',
  templateUrl: './all-books.component.html',
  styleUrls: ['./all-books.component.css']
})
export class AllBooksComponent implements OnInit {
  allBooks: any;
  constructor(private route: ActivatedRoute, private booksService: BooksService, private authenticationService: AuthenticationService) { }
  ngOnInit() {
    this.allBooks = this.booksService.getAllBooks(this.authenticationService.getCurrentUser().id);
    this.allBooks = this.allBooks.filter(book => {
      return book.status !== 'ISSUED' && book.status !== 'EXPIRED';
    });
  }


}



