import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ReaderComponent } from './reader.component';
import { AuthGuard } from '../core/guard/auth.guard';
import { AllBooksComponent } from './all-books/all-books.component';
import { IssuedbooksComponent } from './issuedbooks/issuedbooks.component';
import { BookDetailsComponent } from './all-books/book-details/book-details.component';

const routes: Routes = [
  {
    path: 'reader', component: ReaderComponent, canActivate: [AuthGuard], children: [
      { path: '', redirectTo: 'books', pathMatch: 'full' },
      { path: 'books', component: AllBooksComponent },
      { path: 'books/:bookid', component: BookDetailsComponent },
      { path: 'issued-books', component: IssuedbooksComponent }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  providers: []
})
export class ReaderRoutingModule { }



