import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { ReaderRoutingModule } from './reader-routing.module';
import { ReaderComponent } from './reader.component';
import { AllBooksComponent } from './all-books/all-books.component';
import { BookThumbnailComponent } from './all-Books/book-thumbnail/book-thumbnail.component';
import { IssuedbooksComponent } from './issuedbooks/issuedbooks.component';
import { SearchFilter } from './all-books/all-books.component';
import { BookListComponent } from './issuedbooks/book-list/book-list.component';
import { BookDetailsComponent } from './all-books/book-details/book-details.component';
import { BooksService } from '../core/books.service';

@NgModule({
  imports: [
    CommonModule,
    ReaderRoutingModule
  ],
  declarations: [ReaderComponent,
    AllBooksComponent,
    BookThumbnailComponent,
    IssuedbooksComponent,
    SearchFilter,
    BookListComponent,
    BookDetailsComponent],
  providers: [BooksService]
})
export class ReaderModule { }



