import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { FacebookModule } from 'ngx-facebook';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HeaderComponent } from './shared/header/header.component';
import { ReaderModule } from './reader/reader.module';
import { AdminModule } from './admin/admin.module';
import { HeaderProfileToolbarComponent } from './shared/header/header-profile-toolbar/header-profile-toolbar.component';
import { LoginComponent } from './shared/login/login.component';
import { AuthGuard } from './core/guard/auth.guard';
import { AuthenticationService } from './core/authentication.service';
import { GlobalEventsManager } from './core/global-events-manager.service';
import { BooksService } from './core/books.service';
@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    HeaderProfileToolbarComponent,
    LoginComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    ReaderModule,
    AdminModule,
    AppRoutingModule,
    FacebookModule.forRoot(),
  ],
  providers: [AuthGuard, AuthenticationService, GlobalEventsManager, BooksService],
  bootstrap: [AppComponent]
})
export class AppModule { }



