export class Book {
    id: string;
    isbn: string;
    title: string;
    category: string;
    issuedBy: IssuedBy[];
    location: string;
    copies: string;
    imageUrl: string;
    author: string[];
    description: string;
}
class IssuedBy {
    issuedBy: string;
    issuedOn: string;
    returnDue: string;
}

export class BookResponse {
    title: string;
    authors: string[];
    thumbnailUrl: string;
    description: string;
}



