import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../../../core/authentication.service';

@Component({
  selector: 'atl-header-profile-toolbar',
  templateUrl: './header-profile-toolbar.component.html',
  styleUrls: ['./header-profile-toolbar.component.css']
})
export class HeaderProfileToolbarComponent implements OnInit {
  currentUser: any;
  adminLinkVisible: boolean;
  settingDisabled = true;
  constructor(private authenticationService: AuthenticationService, private router: Router) { }

  ngOnInit() {
    this.currentUser = this.authenticationService.getCurrentUser();
    if (this.authenticationService.isAdmin(this.currentUser)) {
      this.adminLinkVisible = true;
    }
  }
  logout() {
    this.authenticationService.logout();
    window.location.replace('/');
  }

}



