import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import 'rxjs/add/operator/debounceTime';

import { BooksService } from '../../core/books.service';
import { BookResponse } from 'app/core/book';
import { AbstractControl } from '@angular/forms/src/model';

@Component({
  selector: 'atl-add-book',
  templateUrl: './add-book.component.html',
  styleUrls: ['./add-book.component.css']
})
export class AddBookComponent implements OnInit {
  private validationMessages = {
    required: 'This field is required',
    pattern: 'This field is invalid'
  };
  searchISBNFailed = false;
  isbnValidationMessage: string;
  bookForm: FormGroup;
  searchingBookDetails: boolean;
  constructor(private fb: FormBuilder, private bookService: BooksService) { }

  ngOnInit() {
    this.bookForm = this.fb.group({
      isbn: ['', Validators.required],
      title: ['', Validators.required],
      category: ['', Validators.required],
      location: ['', Validators.required],
      copies: ['', Validators.required],
      imageUrl: ['', Validators.required],
      authors: ['', Validators.required],
      description: ['', Validators.required]
    });

    const isbnControl = this.bookForm.get('isbn');
    isbnControl.valueChanges.debounceTime(2000).subscribe(value => {
      this.setMessage(isbnControl);
    });

  }
  onSubmit(book) {
    console.log(book);
  }
  onImageOpenClick() {
    const imageUrl = this.bookForm.get('imageUrl').value;
    window.open(imageUrl, '_blank');
  }
  onSearchISBNClicked() {
    this.bookForm.disable();
    this.searchingBookDetails = true;
    this.bookService.searchBookByISBN(this.bookForm.get('isbn').value).subscribe((bookDetail: BookResponse) => {
      if (bookDetail) {
        this.bookForm.patchValue({
          title: bookDetail.title,
          imageUrl: bookDetail.thumbnailUrl,
          description: bookDetail.description,
          authors: bookDetail.authors
        });
      } else {
        this.searchISBNFailed = true;
      }
      this.bookForm.enable();
    });
  }
  setMessage(c: AbstractControl): void {
    this.isbnValidationMessage = '';
    this.searchISBNFailed = false;
    if ((c.touched || c.dirty) && c.errors) {
      this.isbnValidationMessage = Object.keys(c.errors).map(key =>
        this.validationMessages[key]).join(' ');
    }
  }
}



