import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class GlobalEventsManager {

    private _showNavBar: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
    public showNavBarEmitter: Observable<boolean> = this._showNavBar.asObservable();

    public _addBookClicked: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
    public addBookClickedEmitter: Observable<boolean> = this._addBookClicked.asObservable();

    public _viewBookDetailsClicked: BehaviorSubject<string> = new BehaviorSubject<string>('');
    public viewBookDetailsClickedEmitter: Observable<string> = this._viewBookDetailsClicked.asObservable();

    constructor() { }

    showNavBar(ifShow: boolean) {
        this._showNavBar.next(ifShow);
    }
    addBookClicked(ifClicked: boolean) {
        this._addBookClicked.next(ifClicked);
    }
    viewBookDetailsClicked(isbnValue: string) {
        this._viewBookDetailsClicked.next(isbnValue);
    }
}



