import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, RouterStateSnapshot } from '@angular/router';
import { FacebookService, InitParams, LoginResponse, LoginOptions, AuthResponse, LoginStatus } from 'ngx-facebook';

import { AuthenticationService } from '../../core/authentication.service';
import { GlobalEventsManager } from '../../core/global-events-manager.service';

@Component({
  selector: 'atl-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  user: any = {};
  loading = false;
  returnUrl: string;
  loginOptions: LoginOptions;


  constructor(
    private facebookService: FacebookService,
    private route: ActivatedRoute,
    private router: Router,
    private authenticationService: AuthenticationService,
    private globalEventsManager: GlobalEventsManager) {
    const initParams: InitParams = {
      appId: '156345135099824',
      xfbml: true,
      version: 'v2.11'
    };
    facebookService.init(initParams);
  }

  ngOnInit() {
    this.authenticationService.logout();
    this.loginOptions = {
      scope: 'public_profile,email,pages_messaging',
      return_scopes: true,
      enable_profile_selector: true
    };
    this.facebookService.getLoginStatus().then((loginStatus: LoginStatus) => {
      if (loginStatus.status === 'connected') {
        this.facebookService.api('/me?fields=id,name,picture').then((response: any) => {
          this.user.fb_id = response.id;
          this.user.name = response.name;
          this.user.imageUrl = response.picture.data.url;
          this.authenticationService.login(this.user).then((result: boolean) => {
            if (result) {
              this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
            }
            this.globalEventsManager.showNavBar(true);
            this.router.navigateByUrl(this.returnUrl);
          });
        });
      }
    });
  }

  login() {
    this.facebookService.login(this.loginOptions).then((loginResponse: LoginResponse) => {
      console.log('loginResponse', loginResponse);
      if (loginResponse.status === 'connected') {
        this.facebookService.api('/me?fields=id,name,picture').then((response: any) => {
          this.user.fb_id = response.id;
          this.user.name = response.name;
          this.user.imageUrl = response.picture.data.url;
          this.authenticationService.login(this.user).then((result: boolean) => {
            if (result) {
              this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
            }
            this.globalEventsManager.showNavBar(true);
            this.router.navigateByUrl(this.returnUrl);
          });
        });
      } else {
        console.log('Can not log you in');
      }
    }).catch(err => {
      console.log('Error: Not able to log you in.');
      console.log(err);
    });
  }
}



