import { Component, OnInit, Input } from '@angular/core';
import { AuthenticationService } from '../../core/authentication.service';
import { BooksService } from '../../core/books.service';

@Component({
  selector: 'atl-issuedbooks',
  templateUrl: './issuedbooks.component.html',
  styleUrls: ['./issuedbooks.component.css']
})
export class IssuedbooksComponent implements OnInit {
  issuedBooks: any;
  allBooks: any;

  constructor(private booksService: BooksService, private authenticationService: AuthenticationService) { }

  ngOnInit() {
    this.allBooks = this.booksService.getAllBooks(this.authenticationService.getCurrentUser().id);
    this.issuedBooks = this.allBooks.filter(book => {
      return book.status === 'ISSUED' || book.status === 'EXPIRED';
    });
  }

}



